define('app/modules/dashboard/controllers/dashboard',[
    'angular',
    'text!./../../login/login.html',
    './../../login/controllers/login'
  ],
  function (angular, templateLogin, loginCtrl) {
    'use strict';

    var controller = [
      '$scope',
      '$http',
      '$modal',
      'jsonResource',
      'userFactory',
      '$templateCache',
      '$location',
      'feedFactory',
      'profileFactory',
      function ($scope, $http, $modal, jsonResource, userFactory, $templateCache,
                $location, feedFactory, profile) {

        $templateCache.put('login', templateLogin);

        if (!userFactory.isLoggedIn) {
          $modal.open({
            template: templateLogin,
            controller: loginCtrl,
            backdrop: 'static',
            keyboard: false
          });
        }

        $scope.user = userFactory.getInfo();
        $scope.isTable = true;

        // show 60 new posts handler
        $scope.newPosts = function () {
          console.log('viewsFilterPopularityClick');
        };

        // Added By Nois
        $scope.changeFeed = function (t) {
          $scope.filter.topic = t;
        };

        $scope.filter = {
          type: '',
          datestamp: 'recent',
          topic: 'mainfeed'
        };

        $scope.changeFilter = function (newF) {
          if (newF.type === $scope.filter.type) {
            $scope.filter.type = "";
          } else if (newF.datestamp === $scope.filter.datestamp) {
            $scope.filter.datestamp = "";
          } else {
            angular.extend($scope.filter, newF);
          }
        };

        $scope.$watch('filter', function (newValue, oldValue) {
          var isMentionsfeedPublic = newValue.topic === 'mentionsfeed:public',
              isMentionsFeedPrivate = newValue.topic === 'mentionsfeed:private';

          $scope.isMentionsFeed = isMentionsfeedPublic || isMentionsFeedPrivate;

        }, true);
      }
    ];

    return controller;
  });

