/* istanbul ignore next */
define('app/modules/registration/registration',[
    'angular',
    'angularRoute',
    'text!./registration.html',
    './controllers/registration'
  ],
  function (angular,
            angularRoute,
            templateRegistration,
            registrationCtrl) {
    'use strict';

    var module = angular.module('ernr.registration', ['ngRoute']);

    module.config(
      ['$routeProvider', '$locationProvider', '$httpProvider',
        function ($routeProvider, $locationProvider, $httpProvider) {
          $routeProvider
            .when('/registration', {
              template: templateRegistration,
              controller: registrationCtrl
            });
        }
      ]
    );
    // Not used currently
    return module;
  });
