/* istanbul ignore next */
define('app/modules/about/about',['require','angular','text!./about.html','./controllers/about'],function(require) {
    'use strict';
    var angular = require('angular'),
        templateAbout = require('text!./about.html'),
        aboutCtrl = require('./controllers/about'),
        module;

    module = angular.module('ernr.about', ['ngRoute']);

    module.run(['$templateCache', function ($templateCache) {
      $templateCache.put('about', templateAbout);
    }]);

    module.config(
      ['$routeProvider',
        function ($routeProvider) {
          $routeProvider
            .when('/about', {
              template: templateAbout,
              controller: aboutCtrl
            });
        }
      ]
    );
    // Not used currently
    return module;
});
