define('app/modules/gallery/controllers/gallery',['require'],function (require) {
  'use strict';

  var controller = function ($scope, $location, $routeParams) {
    $scope.isLoading = true;
    $scope.params = $location.search();
    $scope.tag = $routeParams.tag;

    $scope.topic = "tag:" + $scope.tag;

    $scope.setType = function (type) {
      if (type === $scope.params.type) {
        delete $scope.params.type;
      } else {
        $scope.params.type = type;
      }
    };

    $scope.setSort = function (sort) {
      if (!sort || sort === $scope.params.sort) {
        delete $scope.params.sort;
      } else {
        $scope.params.sort = sort;
      }
    };

    $scope.$watch('params', function (newValue, oldValue) {
      $location.search(newValue);
    }, true);
  };

  return ['$scope', '$location', '$routeParams', controller];
});

