define('app/modules/dashboard/directives/userHistory',[
  'angular',
  'lodash',
  'text!../templates/userHistory.html',
  '../services/userHistoryService'
],
function (angular, _, userHistoryTpl) {
  'use strict';

  var module = angular.module('ernr.directives.userHistory', [
    'ernr.dashboard.services.userHistory'
  ])
    .directive('userHistory', function () {
      return {
        restrict: 'E',
        scope: {
          feedId: '=',
          posts: '=ngModel'
        },
        compile: function(element, attrs) {
          if (attrs.ngModel === undefined) {
            attrs.$set("ngModel", "posts");
          }
        },
        controller: ['$scope', 'userHistoryFactory', function ($scope, history) {
          $scope.posts = [];
          $scope.page = 1;
          $scope.per_page = 20;
          $scope.onLoading = false;
          $scope.stopLoadMore = false;

          function update (newValue, oldValue) {
            if ($scope.onLoading || newValue === oldValue) {
              return false;
            }

            $scope.posts = [];
            $scope.page = 1;
            $scope.per_page = 20;
            $scope.onLoading = false;
            $scope.stopLoadMore = false;
            $scope.loadMore();
          }

          $scope.loadMore = function () {
            if ($scope.onLoading || $scope.stopLoadMore) {
              return;
            }

            $scope.onLoading = true;

            history.get({ page: $scope.page, per_page: $scope.per_page }, $scope.feedId)
              .success(function (data) {
                var items = data.result.items;
                $scope.posts = $scope.posts.concat(items);
                $scope.page++;
                $scope.stopLoadMore = items.length < $scope.per_page;
                $scope.onLoading = false;
              });
          };

          $scope.$watch('feedId', update);
        }],
        template: userHistoryTpl
      };
    });

  return module;
});

