define('app/modules/login/controllers/login',['require','angular','text!./../forgot-password.html','./password'],function (require) {
    'use strict';

    var angular = require('angular'),
        templatePassword = require('text!./../forgot-password.html'),
        passwordCtrl = require('./password'),
        controller;

    controller = ['$scope', '$modal', 'apiUserService', '$templateCache', '$modalInstance',
        function ($scope, $modal, apiUserService, $templateCache, $modalInstance) {
            $templateCache.put('password', templatePassword);

            $scope.loginFormValues = {
                login: '',
                password: ''
            };

            $scope.forgotPasswordModal = function () {
                $modal.open({
                    template: templatePassword,
                    controller: passwordCtrl
                });
            };

            $scope.onSubmitHandler = function ($event, loginForm) {
                $event.preventDefault();

                if (loginForm && loginForm.$valid && $scope.loginFormValues.login && $scope.loginFormValues.password) {
                    apiUserService.login($scope.loginFormValues).then(function () {
                        $modalInstance.close();
                    });
                }
            };
        }
    ];

    return controller;
});
