define('app/modules/login/controllers/validation',['require','angular'],function(require) {
	'use strict';

	var angular = require('angular'),
		controller;

	controller = ['$scope', 'apiUserService',
		function ($scope, apiUserService) {
			$scope.validationFormValues = {
				username: '',
				code: ''
			};
			$scope.errorMessage = false;

			$scope.onSubmitValidationHandler = function ($event) {
				$event.preventDefault();
				if ($scope.validationForm && $scope.validationForm.$valid && $scope.validationFormValues.username && $scope.validationFormValues.code) {
					apiUserService.validation($scope.validationFormValues);
				}
			};
		}
	];

	return controller;
});

