define('app/modules/dashboard/controllers/favorites',['angular'], function (angular) {
  'use strict';

  var controller = function ($scope, favorites) {
    $scope.posts = [];
    $scope.page = 1;
    $scope.per_page = 20;
    $scope.onLoading = false;
    $scope.stopLoadMore = false;

    $scope.loadMore = function () {
      if ($scope.onLoading || $scope.stopLoadMore) {
        return;
      }

      $scope.onLoading = true;

      favorites.get({ page: $scope.page, per_page: $scope.per_page })
        .success(function (data) {
          var items = data.result.items;
          $scope.posts = $scope.posts.concat(items);
          $scope.page++;
          $scope.stopLoadMore = data.result.items < $scope.per_page;
          $scope.onLoading = false;
        });
    };
  };

  return [
    '$scope',
    'favoritesFactory',
    controller
  ];
});

