/* istanbul ignore next */
define('app/modules/dashboard/dashboard',[
    'angular',
    'angularRoute',
    'angularScroll',
    'angularBootstrap',
    'app/common/resources/api',
    'app/common/services/favoritesService',
    'app/common/services/followService',
    'app/common/resources/jsonresource',
    'text!./dashboard.html',
    './controllers/dashboard',
    'text!./templates/paymentsHistory.html',
    'text!./templates/paymentsSettings.html',
    './controllers/payments',
    'text!./templates/favorites.html',
    './controllers/favorites',
    'text!./templates/following.html',
    'text!./templates/followers.html',
    './controllers/following',
    './controllers/followers',
    './directives/userFeed',
    './directives/userHistory',
    './controllers/history',
    'text!./templates/history.html',
    './controllers/pending',
    'text!./templates/pending.html',
    './controllers/tags',
    'text!./templates/tags.html',
    './controllers/blocked',
    'text!./templates/blocked.html',
    'app/common/directives/feedFilters/feedFilters',
    'app/common/directives/follow/followers',
    'app/common/directives/follow/following',
    'app/common/directives/userCard/userCard',
    'app/common/services/profileService',
    'app/common/services/tagsService',
    'app/common/services/blockListService'
  ],
  function (angular,
            angularRoute,
            angularScroll,
            angularBootstrap,
            apiServiceModule,
            favoritesServiceModule,
            followServiceModule,
            jsonresourceModule,
            templateDashboard,
            dashboardCtrl,
            templatePaymentsHistory,
            templatePaymentsSettings,
            paymentsCtrl,
            templateFavorites,
            favoritesCtrl,
            templateFollowing,
            templateFollowers,
            followingCtrl,
            followersCtrl,
            userFeed,
            userHistory,
            historyCtrl,
            templateHistory,
            pendingCtrl,
            templatePending,
            tagsCtrl,
            templateTags,
            blockedCtrl,
            templateBlocked) {
    'use strict';

    var module = angular.module('ernr.dashboard', [
      'ngRoute',
      'common.resources.apiService',
      'ui.bootstrap',
      'infinite-scroll',
      'ernr.favoritesService',
      'ernr.followService',
      'common.resources.jsonresource',
      'ernr.directives.userFeed',
      'ernr.directives.feedFilters',
      'ernr.directives.followers',
      'ernr.directives.following',
      'ernr.directives.userCard',
      'ernr.ProfileService',
      'ernr.directives.userHistory',
      'ernr.tagsService',
      'ernr.blockListService'
    ]);

    module.run(['$templateCache', function ($templateCache) {
      $templateCache.put('dashboard', templateDashboard);
      $templateCache.put('paymentsHistory', templatePaymentsHistory);
      $templateCache.put('paymentsSettings', templatePaymentsSettings);
    }]);

    module.config(
      ['$routeProvider',
        function ($routeProvider) {
          $routeProvider
            .when('/dashboard', {
              template: templateDashboard,
              controller: dashboardCtrl
            })
            .when('/dashboard/payments/history', {
              template: templatePaymentsHistory,
              controller: paymentsCtrl
            })
            .when('/dashboard/payments/settings', {
              template: templatePaymentsSettings,
              controller: paymentsCtrl
            })
            .when('/dashboard/favorites', {
              template: templateFavorites,
              controller: favoritesCtrl
            })
            .when('/dashboard/following', {
              template: templateFollowing,
              controller: followingCtrl
            })
            .when('/dashboard/history/:page', {
              template: templateHistory,
              controller: historyCtrl
            })
            .when('/dashboard/followers', {
              template: templateFollowers,
              controller: followersCtrl,
            })
            .when('/dashboard/pending', {
              template: templatePending,
              controller: pendingCtrl,
            })
            .when('/dashboard/tags', {
              template: templateTags,
              controller: tagsCtrl,
            })
            .when('/dashboard/blocked', {
              template: templateBlocked,
              controller: blockedCtrl,
            });
        }
      ]
    );

    return module;
  });

