define('app/modules/dashboard/controllers/tags',['angular'], function (angular) {
  'use strict';

  var controller = function ($scope, tags) {
    $scope.tags = [];
    $scope.page = 1;
    $scope.per_page = 20;
    $scope.onLoading = false;
    $scope.stopLoadMore = false;

    $scope.loadMore = function () {
      if ($scope.onLoading || $scope.stopLoadMore) {
        return;
      }

      $scope.onLoading = true;

      tags.getFollowing({ page: $scope.page, per_page: $scope.per_page })
        .success(function (data) {
          var items = data.result.tags;
          $scope.tags = $scope.tags.concat(items);
          $scope.page++;
          $scope.stopLoadMore = data.result.size < $scope.per_page;
          $scope.onLoading = false;
        });
    };
  };

  return [
    '$scope',
    'tagsFactory',
    controller
  ];
});


