define('app/modules/login/controllers/password',['require','angular'],function(require) {
	'use strict';

	var angular = require('angular'),
		controller;

	controller = ['$scope', 'apiUserService',
		function ($scope, apiUserService) {
			$scope.passwordFormValues = {
				email: ''
			};

			$scope.onSubmitPasswordHandler = function ($event, passwordForm) {
				$event.preventDefault();
				if (passwordForm && passwordForm.$valid && $scope.passwordFormValues.email) {
					apiUserService.password($scope.passwordFormValues);
				}
			};
		}
	];

	return controller;
});
