define('app/modules/dashboard/controllers/payments',[],function() {
	'use strict';

	var controller = ['$scope',
		function ($scope) {
			$scope.card = 'paypal';

			$scope.data = {};

			$scope.onClickTab = function (card) {
				$scope.card = card;
			};

			$scope.submitHandler = function ($event) {
				$event.preventDefault();
			};
		}
	];

	return controller;
});

