/* istanbul ignore next */
define('app/modules/login/login',[
    'angular',
    'angularRoute',
    'app/common/resources/api',
    'text!./login.html',
    './controllers/login',
    'text!./validation.html',
    './controllers/validation'
  ],
  function (angular,
            angularRoute,
            apiService,
            templateLogin,
            loginCtrl,
            templateValidation,
            validationCtrl) {
    'use strict';

    var module = angular.module('ernr.login', ['ngRoute', 'common.resources.apiService']);

    module.run(['$templateCache', function ($templateCache) {
      $templateCache.put('login', templateLogin);
      $templateCache.put('validation', templateValidation);
    }]);

    module.config(
      ['$routeProvider',
        function ($routeProvider) {
          $routeProvider
            .when('/login', {
              template: templateLogin,
              controller: loginCtrl
            })
            .when('/login/validation', {
              template: templateValidation,
              controller: validationCtrl
            });
        }
      ]
    );
    // Not used currently
    return module;
  });
