define('app/modules/hashtags/controllers/hashtags',['require'],function (require) {
  'use strict';

  var controller = function ($scope, tags, $location) {
    $scope.tags = [];
    $scope.page = 1;
    $scope.per_page = 20;
    $scope.onLoading = false;
    $scope.stopLoadMore = false;
    $scope.params = $location.search();

    $scope.loadMore = function () {
      if ($scope.onLoading || $scope.stopLoadMore) {
        return;
      }

      $scope.onLoading = true;

      tags.getList({ page: $scope.page, per_page: $scope.per_page })
        .success(function (data) {
          var items = data.result.items;
          $scope.tags = $scope.tags.concat(items);
          $scope.page++;
          $scope.stopLoadMore = items.length < $scope.per_page;
          $scope.onLoading = false;
        });
    };

    $scope.setSort = function (sort) {
      if (sort === $scope.params.sort) {
        delete $scope.params.sort;
      } else {
        $scope.params.sort = sort;
      }
    };

    $scope.$watch('params', function (newValue, oldValue) {
      $location.search(newValue);
    }, true);
  };

  return ['$scope', 'tagsFactory', '$location', controller];
});

