/* istanbul ignore next */
define('app/modules/gallery/gallery',['require','angular','text!./templates/gallery.html','./controllers/gallery'],function (require) {
  'use strict';
  var angular = require('angular'),
    templateGallery = require('text!./templates/gallery.html'),
    galleryCtrl = require('./controllers/gallery'),
    module;

  module = angular.module('ernr.gallery', ['ngRoute']);

  module.run(['$templateCache', function ($templateCache) {
    $templateCache.put('gallery', templateGallery);
  }]);

  module.config(
    ['$routeProvider',
      function ($routeProvider) {
        $routeProvider
          .when('/tags/:tag', {
            template: templateGallery,
            controller: galleryCtrl
          });
      }
    ]
  );

  return module;
});

