define('app/modules/dashboard/services/userFeedService',[
  'angular',
  'app/common/resources/constants/constants'
],
function (angular) {
  'use strict';

  return angular.module('ernr.dashboard.services.userFeed', [
    'common.resources.constant'
  ])
    .factory('userFeedFactory', [
      '$http',
      'apiDomain',
      function ($http, constant) {
        return {
          get: function (params, feedId) {
            return $http.get(constant.domain + '/userfeeds/' + feedId, { params: params });
          }
        };
      }
    ]);
});

