define('app/modules/dashboard/controllers/pending',['require'],function (require) {
  'use strict';

  var controller = function ($scope, $interval, userFeed) {
    var timer = $interval(function () {
      if (!$scope.feeds.length) {
        return;
      }

      userFeed.get({ page: 1, per_page: $scope.feeds.length }, $scope.feedId)
        .success(function (data) {
          var items = data.result.items;
          $scope.feeds = items;
        });
    }, 5000);

    $scope.feedId = 'pending';

    $scope.$on("$destroy", function() {
      if (timer) {
        $interval.cancel(timer);
      }
    });
  };

  return ['$scope', '$interval', 'userFeedFactory', controller];
});

