define('app/modules/dashboard/controllers/blocked',['angular'], function (angular) {
  'use strict';

  var controller = function ($scope, blockList) {
    $scope.users = [];
    $scope.page = 1;
    $scope.per_page = 20;
    $scope.onLoading = false;
    $scope.stopLoadMore = false;

    $scope.isBlocked = function (id) {
      return blockList.isBlocked(id);
    };

    $scope.loadMore = function () {
      if ($scope.onLoading || $scope.stopLoadMore) {
        return;
      }

      $scope.onLoading = true;

      blockList.get({ page: $scope.page, per_page: $scope.per_page })
        .success(function (data) {
          var users = data.result.items;
          $scope.users = $scope.users.concat(users);
          $scope.page++;
          $scope.stopLoadMore = data.result.size < $scope.per_page;
          $scope.onLoading = false;
        });
    };
  };

  return [
    '$scope',
    'blockListFactory',
    controller
  ];
});



