define('app/modules/hashtags/hashtags',['require','angular','text!./templates/hashtags.html','./controllers/hashtags'],function (require) {
  'use strict';

  var angular = require('angular'),
      template = require('text!./templates/hashtags.html'),
      ctrl = require('./controllers/hashtags'),
      module;

  module = angular.module('ernr.terms', ['ngRoute']);

  module.run(['$templateCache', function ($templateCache) {
    $templateCache.put('hashtags', template);
  }]);

  module.config(['$routeProvider', function ($routeProvider) {
    $routeProvider
      .when('/hashtags', {
        template: template,
        controller: ctrl
      });
  }]);

  return module;
});

