define('app/modules/dashboard/directives/userFeed',[
  'angular',
  'lodash',
  'text!../templates/userFeed.html',
  '../services/userFeedService',
  './pendingPost',
  'app/common/directives/feed/feedFull'
],
function (angular, _, mainFeedTpl) {
  'use strict';

  var module = angular.module('ernr.directives.userFeed', [
    'ernr.dashboard.services.userFeed',
    'ernr.directives.pendingPost',
    'ernr.directives.feedFull'
  ])
    .directive('userFeed', function () {
      return {
        restrict: 'E',
        scope: {
          feedId: '=',
          type: '=',
          sort: '=',
          feeds: '=ngModel',
          table: '='
        },
        compile: function(element, attrs) {
          if (attrs.ngModel === undefined) {
            attrs.$set("ngModel", "feeds");
          }
        },
        controller: ['$scope', 'userFeedFactory', function ($scope, userFeed) {
          $scope.feeds = [];
          $scope.page = 1;
          $scope.per_page = 20;
          $scope.onLoading = false;
          $scope.stopLoadMore = false;

          function update (newValue, oldValue) {
            if ($scope.onLoading || newValue === oldValue) {
              return false;
            }

            $scope.feeds = [];
            $scope.page = 1;
            $scope.per_page = 20;
            $scope.onLoading = false;
            $scope.stopLoadMore = false;
            $scope.loadMore();
          }

          $scope.loadMore = function () {
            if ($scope.onLoading || $scope.stopLoadMore) {
              return;
            }

            var q = _.compact([$scope.feedId, $scope.type, $scope.sort]).join(':');
            $scope.onLoading = true;

            userFeed.get({ page: $scope.page, per_page: $scope.per_page }, q)
              .success(function (data) {
                var items = data.result.items;
                $scope.feeds = $scope.feeds.concat(items);
                $scope.page++;
                $scope.stopLoadMore = items.length < $scope.per_page;
                $scope.onLoading = false;
              });
            $scope.$watch('table', function (newValue) {
              $scope.isTable = newValue;
            });
          };

          $scope.newPostsClick = function () {
            return false;
          };

          $scope.$watch('sort', function (newValue, oldValue) {
            if (newValue === 'recent') {
              $scope.sort = '';
            }

            update(newValue, oldValue);
          });

          $scope.$watch('feedId', update);
          $scope.$watch('type', update);

        }],
        template: mainFeedTpl
      };
    });

  return module;
});

